.gallery {
  margin: 0 auto;
  max-width: 880px;
  padding-top: 51px;
}

@media screen and (min-width: 690px) and (max-width: 879px){
  .gallery{
    max-width: 690px;
  }
}

@media screen and (min-width: 319px) and (max-width: 679px){
  .gallery{
    max-width: 320px;
    padding-top: 36px;
  }
}