.popup{
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transition: visibility 0.3s, opacity 0.3s linear;
  visibility: hidden;
  opacity: 0;
}